
import { Row, Col,Form,Button,InputGroup,FormControl,Modal,FormCheck} from 'react-bootstrap'
import Card from '../../../components/card/card';
import "./list.css"
import React, { useEffect } from 'react'

import Alert from '../../../components/toast/toast'
import { loadingToggleAction } from "../../../store/action/common";
import store from "../../../store/store";
import ApiService from '../../../services/service'
import { API_NAME } from '../../../utils/constants';
import { useState } from 'react';
import ReactPaginate from 'react-paginate';

import { ToastContainer } from 'react-toastify'
import Spinner from '../../../components/loader/loader'
import { connect } from 'react-redux'


const TournamentList = (props)=>{


    const [showModal, setShowModal] = useState(false)
    const [tournaments, setTournaments] = React.useState([])
    const [pageNumber, setPageNumber] = React.useState(0)
    const [users, setUsers] = React.useState([])
    const [delPage, setDelPage] = React.useState(0)

    const filtered = tournaments
    const tournamentPerPage = 6
    const pagesVisited = pageNumber * tournamentPerPage
 
    const pageCount = Math.ceil(filtered.length / tournamentPerPage)
 
    const changePage = ({ selected }) => {
       setPageNumber(selected)
    }
    const showEdit = () => {
        document.activeElement.blur();
        if (showModal)
            setShowModal(false)
        else
            setShowModal(true)
    }

    const [role, setRole] = useState([
        {
            name: 'Admin',
            status: true
        },
    ])
    
    const fetchUsers = () => {
        store.dispatch(loadingToggleAction(true))
        console.log('Component : User List')
        console.log("Request : Get")
        ApiService.getData(API_NAME.USER).then(
           (resData) => {
  
              if(resData.statusCode==='00')
              {
                 store.dispatch(loadingToggleAction(false))
                // Alert('00', resData.message)
                 console.log('===================================')
                 setUsers(resData.data)
              }
              else
              {
                 store.dispatch(loadingToggleAction(false))
               //  Alert('01', resData.message)
                 console.log('==================================')
              }
            
  
           }
        ).catch((err) => {
           store.dispatch(loadingToggleAction(false))
           Alert('01', err.message)
           console.log('==================================')
  
        });
     }

    const fetchTournaments = (tag) => {
        store.dispatch(loadingToggleAction(true))
        console.log('Component : Tournament List')
        console.log("Request : Get")
        ApiService.getData(API_NAME.TOURNAMENT_LIST).then(
           (resData) => {
  
              if(resData.statusCode==='00')
              {
                 store.dispatch(loadingToggleAction(false))
                 if(tag)
                 Alert('00', resData.message)
                 console.log('===================================')
                 setTournaments(resData.data)
                 setUsers(resData.userList)
              }
              else
              {
                 store.dispatch(loadingToggleAction(false))
                 Alert('01', resData.message)
                 console.log('==================================')
              }
            
  
           }
        ).catch((err) => {
           store.dispatch(loadingToggleAction(false))
           Alert('01', err.message)
           console.log('==================================')
  
        });
     }
  
     const deleteTournament=(tourId)=>{
        let params = {
            tour_id: tourId
         }
        store.dispatch(loadingToggleAction(true))
        console.log('Component : Tournament List')
        console.log("Request : Post")
        ApiService.postData(API_NAME.DELETE_TOURNAMENT, params).then(
           (resData) => {
  
              if(resData.statusCode==='00')
              {
                 store.dispatch(loadingToggleAction(false)) 
                 Alert('00', resData.message)
                 fetchTournaments(0);
                 setPageNumber(0);
                 setDelPage(0);
                 console.log('===================================')
              }
              else
              {
                 store.dispatch(loadingToggleAction(false))
                 Alert('01', resData.message)
                 console.log('==================================')
              }
            
           }
        ).catch((err) => {
           store.dispatch(loadingToggleAction(false))
           Alert('01', err.message)
           console.log('==================================')
  
        });
     }
     
     useEffect(() => {
        fetchTournaments(1);
        // fetchUsers();
     }, [])

     const displayTournament = tournaments
     .slice(pagesVisited, pagesVisited + tournamentPerPage)
     .map((item, idx) => {
        return (
            <Col lg="4" key={idx}>
            <Card className="cardbg">
            <div class={"text-primary " + (item.TournamentState == 'finished' ? 'iq-ribbon-effect-green' : 'iq-ribbon-effect-red')}><span style={{fontSize:'8px',color:'#fff'}}>{item.TournamentState == 'finished' ? 'COMPLETED' : 'ONGOING'}</span></div>
                <Card.Body>
                    <div className="d-flex justify-content-between mb-4">
                        <div className="">
                            <h5>{item.TournamentName}</h5>
                            
                        </div>
                        <div className="" style={{visibility:'hidden'}}>
                            <div className="badge bg-danger p-3">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" fill="none" viewBox="0 0 24 24" stroke="white"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"></path></svg>
                            </div>
                        </div>
                    </div>
                    <div className="d-flex  justify-content-start align-items-center mb-3" style={{marginTop:'40px'}}>
                        <div style={{width:'100%',textAlign:'center'}}>
                            <h6 className="mb-0 text-primary">{item.tournament_type}</h6>
                            <small>{item.Place}</small>
                        </div>
                    </div>

                    <Form action="#" style={{marginTop:'30px'}}>
                            <InputGroup className="mb-3" onFocus={showEdit} >
                                <FormControl type="text" placeholder="Assign user" aria-label="Recipient's username" aria-describedby="basic-addon2"/>
                                <Button type="button" className="input-group-text btn-primary" id="basic-addon2">
                                    <svg width="20" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M12 11a4 4 0 1 0-4-4 4 4 0 0 0 4 4zm6 10a1 1 0 0 0 1-1 7 7 0 0 0-14 0 1 1 0 0 0 1 1z" fill="currentColor"></path>
                                    </svg>
                                </Button>
                            </InputGroup>
                        </Form>

                        <div style={{width:'100%',marginTop:'40px'}}>
                        <Button variant="btn btn-primary" style={{width:'45%'}}>View</Button>
                        <Button variant="btn btn-primary" style={{width:'45%',float:'right'}} onClick={() => deleteTournament(item.Tournamentid)}>Delete</Button>
                        </div>
                   
                </Card.Body>
            </Card>
        </Col>
        )
     })
    return (
        <>
        <ToastContainer />
        <Spinner loading={props.showLoading} />
       <Row>
        {displayTournament}
                <Modal show={showModal} onHide={showEdit} scrollable={true} >
                    <Modal.Header className="btnw" closeButton>
                        <Modal.Title as="h5">Select User</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Form>
                        <Row>
                <Col sm="12">
                    <Card>
          
                        <Card.Body>
                            <div className="table-responsive">
                                <table className="table table-bordered">
                                    
                                    <tbody>
                                        {
                                            users.map((item, index) =>
                                            (
                                                <tr className="" key={index} >
                                                    <td className="">{item.username}
                                                
                                                    </td>
                                                    {role.map((item1, index) => (
                                                        <td className="text-center" key={index}>
                                                            {item1.status && item.status === true ?
                                                                <input className="form-check-input" type="checkbox" defaultChecked />
                                                                : <input className="form-check-input" type="checkbox" />

                                                            }
                                                        </td>
                                                    ))}
                                                  
                                                </tr>
                                            ))
                                        }
                                    </tbody>
                                </table>
                                
                            </div>
                        </Card.Body>
                    </Card>

                </Col>
            </Row>
                            <Form.Group>
                                <Button variant="btn btn-primary" style={{ float: 'right' }} type="submit">Update</Button>

                            </Form.Group>
                            <div style={{ marginTop: '40px' }}></div>
                        </Form>

                    </Modal.Body>
                    {/* <Modal.Footer>
                <Button variant="secondary" >Close</Button>
                <Button variant="primary" >Save changes</Button>
            </Modal.Footer> */}
                </Modal>


                
                   
                </Row>
        {filtered.length > 0 ? <ReactPaginate
               previousLabel={"Previous"}
               nextLabel={"Next"}
               pageCount={pageCount}
               onPageChange={changePage}
               breakClassName={'page-item'}
               breakLinkClassName={'page-link'}
               containerClassName={'pagination'}
               pageClassName={'page-item'}
               pageLinkClassName={'page-link'}
               previousClassName={'page-item'}
               previousLinkClassName={'page-link'}
               nextClassName={'page-item'}
               nextLinkClassName={'page-link'}
               activeClassName={'active'}
               forcePage={delPage}
               className="pagination pagination-md flex-wrap justify-content-center"
            /> : ' '}
    
</>



    )
}

const mapStateToProps = (state) => {
    return {
       showLoading: state.auth.showLoading
    }
 }

export default connect(mapStateToProps)(TournamentList);